	module.exports = {"data":{"markdownRemark":{"html":"<p>当時(高校の頃)、数学の教鞭を振るっていたスー先生ことGさん(仮名)の似顔絵。\n写生したものではなく脳内イメージで描いたものだが、改めて顔を見ると恐ろしくそっくりだった覚えがある。</p>\n<p>当時同級生の<a href=\"http://mixi.jp/show_friend.pl?id=2308126\">バタービーン氏</a>が制作していた “<a href=\"/2898\">JIJIHAZARD</a>” の設定用イラストとして作成した絵。</p>\n<p>あの日の授業の超展開は今でも忘れない……\nミミズの這ったようなとても字には見えない物体を黒板に書いていく G 先生。</p>\n<ol>\n<li>先生「おい、まく、この問題分かるか？」</li>\n<li>まく「なんて書いてあるのか読めません」</li>\n<li>先生「バカヤロー！小学生でも出来る問題だぞ！」</li>\n</ol>","frontmatter":{"cover":null,"date":"1997-10-01T00:00:00+09:00","strDate":"1997/9/30","title":"Gさん"}}},"pathContext":{"path":"/ja/blog/1997/10/01-mr-g/","langKey":"ja"}}

